.markdown-viewer {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    line-height: 1.6;
  }
  
  .markdown-viewer h1, .markdown-viewer h2, .markdown-viewer h3 {
    margin-top: 20px;
    margin-bottom: 10px;
  }
  
  .markdown-viewer p {
    margin-bottom: 15px;
  }
  
  .markdown-viewer a {
    color: #0066cc;
    text-decoration: none;
  }
  
  .markdown-viewer a:hover {
    text-decoration: underline;
  }