.cutting-edge {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Helvetica', 'Arial', sans-serif;
}

.ce-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
}

.ce-title {
  font-size: 32px;
  font-weight: 600;
  text-align: left;
  margin: 0;
}

.ce-translation-recommendation {
  display: flex;
  align-items: center;
}

.ce-translation-logo {
  width: 30px;
  height: 30px;
  margin-right: 10px;
}

.ce-translation-text {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.ce-translation-text p {
  margin: 0 0 3px 0;
  font-size: 12px;
}

.ce-translation-button {
  display: inline-block;
  padding: 5px 10px;
  background-color: #0071e3;
  color: white;
  text-decoration: none;
  border-radius: 20px;
  font-size: 12px;
  transition: background-color 0.3s;
}

.ce-translation-button:hover {
  background-color: #0077ed;
}

.ce-back-button {
  display: inline-block;
  margin-top: -20px;
  margin-bottom: 20px;
  padding: 8px 16px;
  background-color: #0071e3;
  color: white;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.3s;
  text-align: left;
  position: absolute;
  left: 20px;
  z-index: 10;
}

.ce-back-button:hover {
  background-color: #0077ed;
}

.ce-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 30px;
  margin-top: 100px; /* 增加这个值 */
}

.ce-article-item {
  background-color: #f5f5f7;
  border-radius: 18px;
  padding: 20px;
  transition: transform 0.3s;
}

.ce-article-item:hover {
  transform: scale(1.03);
}

.ce-article-title {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 10px;
}

.ce-article-journal,
.ce-article-category,
.ce-article-date {
  font-size: 14px;
  color: #86868b;
  margin-bottom: 5px;
}

.ce-article-summary {
  font-size: 16px;
  line-height: 1.5;
  margin-bottom: 15px;
}

.ce-article-link {
  display: inline-block;
  padding: 8px 16px;
  background-color: #0071e3;
  color: white;
  text-decoration: none;
  border-radius: 20px;
  font-size: 14px;
  transition: background-color 0.3s;
}

.ce-article-link:hover {
  background-color: #0077ed;
}

.ce-translation-link {
  color: #0071e3;
  text-decoration: none;
  font-size: 14px;
  transition: color 0.3s;
}

.ce-translation-link:hover {
  text-decoration: underline;
}
