.customer-support {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.support-item {
  display: flex;
  align-items: center;
}

.support-icon {
  width: 36px;
  height: 36px;
  margin-right: 10px;
  font-size: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.support-text {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.support-text p {
  margin: 0;
  font-size: 12px;
  color: #1d1d1f;
}

.support-text a {
  color: #0066cc;
  text-decoration: none;
  font-size: 12px;
}

@media (max-width: 768px) {
  .customer-support {
    margin-top: 30px;
    align-items: flex-start;
  }
}
