/* src/App.css */
.App {
  text-align: center;
  font-family: Arial, sans-serif;
}

.App-header {
  background-color: rgba(0, 0, 0, 0.8);
  position: sticky;
  top: 0;
  z-index: 1001;
}

.header-container {
  max-width: 980px;
  margin: 0 auto;
  padding: 0 22px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 980px;
}

.header-left, .header-center, .header-right {
  flex: 1;
  display: flex;
  align-items: center;
}

.header-center {
  justify-content: center;
}

.header-right {
  justify-content: flex-end;
}

.header-icons {
  display: flex;
  align-items: center;
}

.header-icon {
  color: #f5f5f7;
  font-size: 16px;
  margin-left: 15px;
  cursor: pointer;
}

.mobile-menu-icon {
  color: #f5f5f7;
  font-size: 20px;
  margin-left: 15px;
  cursor: pointer;
  display: none;
}

@media (max-width: 768px) {
  .footer-columns {
    flex-direction: column;
    align-items: center;
  }

  .footer-column {
    flex-basis: 100%;
    margin-bottom: 20px;
    text-align: center;
  }

  .header-container {
    max-width: 980px;
    margin: 0 auto;
    padding: 0 22px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .header-left {
    flex: 0 0 auto;
  }

  .header-center {
    display: none;
  }

  .header-right {
    flex: 0 0 auto;
    display: flex;
    align-items: center;
  }

  .mobile-menu-icon {
    display: block;
    margin-left: 20px;
  }

  .mobile-menu {
    display: block;
  }

  .mobile-nav ul {
    flex-direction: column;
    align-items: flex-start;
    padding-top: 60px;
    padding-left: 20px;
    padding-right: 20px;
  }

  .mobile-nav li {
    margin: 15px 0;
    width: 100%;
  }

  .mobile-nav a {
    display: block;
    width: 100%;
    padding: 10px 0;
    font-size: 16px;
  }
}

.logo {
  display: flex;
  align-items: center;
  min-width: 100px;
}
.logo {
  height: 32px; /* 稍微减小 logo 高度 */
}

.main-nav {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
}

.main-nav ul {
  display: flex;
  list-style-type: none;
  padding: 0;
  margin: 0;
  flex-wrap: nowrap;
  justify-content: center;
}

.main-nav li {
  position: relative;
}

.main-nav a {
  color: #f5f5f7;
  text-decoration: none;
  font-size: 12px;
  padding: 0 10px;
  white-space: nowrap;
}

.main-nav a:hover {
  opacity: 1;
}

.banner {
  background-color: #1d1d1f;
  color: #f5f5f7;
  text-align: center;
  padding: 12px 0;
 
}

.banner a {
  color: #2997ff;
  text-decoration: none;
}

.banner a:hover {
  text-decoration: underline;
}

.App-main {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 10px;
  max-width: 980px;
  margin: 0 auto;
}

.app-banner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  border-radius: 18px;
  overflow: hidden;
  position: relative;
  margin-bottom: 10px;
  text-align: center;
}

.quarter-grid {
  display: flex;
  gap: 10px;
}

.quarter {
  flex: 1;
  height: 500px;
}

.banner-content {
  z-index: 1;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.banner-content h2 {
  margin-bottom: 10px;
}

.banner-content p {
  margin-bottom: 20px;
}

.quarter .banner-content {
  position: relative;
  top: 0;
  left: 0;
}

.banner-image {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  max-width: 100%;
  height: auto;
}

.large .banner-image, .gradient .banner-image, .product .banner-image {
  width: 100%;
  height: 100%;
 
}

.quarter .banner-image {
  width: 100%;
  height: 70%;
  top: auto;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.banner-buttons {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-top: 10px;
}

.banner-buttons button {
  padding: 8px 16px;
  border: none;
  border-radius: 20px;
  font-weight: bold;
  cursor: pointer;
  font-size: 14px;
}

.learn-more {
  background-color: #0071e3;
  color: white;
}

.buy-now {
  background-color: transparent;
  color: #0071e3;
}

/* 特定样式 */
.large {
  height: 600px;
  background: #000000;
  color: white;
}

.gradient {
  height: 600px;
  background: linear-gradient(to bottom, #f5f5f7, #ffffff);
  color: black;
}

.product {
  height: 600px;
  background-color: #f5f5f7;
  color: black;
}

.dark {
  background-color: #000000;
  color: white;
}

.lifestyle {
  background-color: #fbfbfd;
  color: black;
}

.minimal {
  background-color: #ffffff;
  border: 1px solid #d2d2d7;
  color: black;
}

.wearable {
  background-color: #f2f2f2;
  color: black;
}

.App-footer {
  background-color: #f5f5f7;
  color: #6e6e73;
  font-size: 12px;
 
}

.footer-content {
  max-width: 980px;
  margin: 0 auto;
  padding: 0 22px;
  text-align: left;
}

.footer-columns {
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid #d2d2d7;
}

.footer-column {
  flex-basis: 100%;
  margin-bottom: 0;
  text-align: left;
  padding: 0;
  border-bottom: 1px solid #d2d2d7;
}

.footer-column:last-child {
  border-bottom: none;
  width: 100%
}

.footer-column h3 {
  font-size: 12px;
  margin-bottom: 10px;
  color: #1d1d1f;
}

.footer-column ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.footer-column li {
  margin-bottom: 8px;
  
}

.footer-column a {
  color: #424245;
  text-decoration: none;
}

.footer-column a:hover {
  text-decoration: underline;
}

.footer-column-links {
  display: none;
  padding: 0 0 10px 0;
  margin: 0;
}

.footer-column-links.expanded {
  display: block;
}

.footer-bottom {
  padding-top: 20px;
  font-size: 12px;
}

.footer-legal {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 10px 0;
}

.footer-legal-right {
  display: flex;
}

.footer-legal-right a {
  color: #424245;
  text-decoration: none;
  border-right: 1px solid #d2d2d7;
  padding-right: 10px;
  margin-right: 10px;
}

.footer-legal-right a:last-child {
  border-right: none;
  padding-right: 0;
  margin-right: 0;
}

