} */

.main-nav .search-icon {
  width: 16px;
  height: 16px;
  background-image: url('./assets/icons/icon_search_default.svg');
  background-size: contain;
  background-repeat: no-repeat;
}

/* 菜单图标样式 */
.menu-icon {
  display: none;
  flex-direction: column;
  cursor: pointer;
}

.menu-icon-bar {
  width: 25px;
  height: 3px;
  background-color: #f5f5f7;
  margin: 4px 0;
}

/* 隐藏菜单项 */
.nav-items {
  display: flex;
}

.nav-items.open {
  display: block;
}

@media (max-width: 768px) {
  .footer-column-title {
