  }

  .quarter {
    height: auto;
  }

  .banner-content h2 {
    font-size: 24px;
  }

  .banner-content p {
    font-size: 14px;
  }

  body {
    font-size: 14px;
  }

  .App-header {
    padding: 10px 0;
  }

  .main-nav a {
    font-size: 14px;
    padding: 0 8px;
  }

  .banner-buttons button {
    padding: 12px 20px;
    font-size: 16px;
  }
}

@supports (-webkit-overflow-scrolling: touch) {
  /* iOS specific styles */
  .App-main {
    -webkit-overflow-scrolling: touch;
  }

  /* 修复 iOS 上可能出现的点击延迟 */
  .banner-buttons button {
    touch-action: manipulation;
  }

  /* 防止 iOS 上的文本自动缩放 */
  body {
    -webkit-text-size-adjust: 100%;
  }

  /* 修复 iOS 上固定定位元素的问题 */
  /* .App-header {
    -webkit-transform: translateZ(0);
  } */
}

